<template>
  <div class="pdf-container">
    <div class="pdf-operate-btn">
      <!-- 下载 start -->
      <el-button
        type="primary"
        @click="onDownloadPDF"
        icon="el-icon-download"
        :loading="downloadLoading"
        >下载</el-button
      >
      <!-- 下载 end -->

      <!-- 核实 start -->
      <el-tag v-if="verifyState == '0'">已核实</el-tag>
      <el-button
        type="primary"
        @click="onVerify"
        icon="el-icon-circle-check"
        :loading="verifyLoading"
        v-if="verifyState == '1'"
        >核实</el-button
      >
      <!-- 核实 end -->
    </div>

    <div ref="tableContent">
      <!-- 顶部表格数据 start -->
      <el-table
        :data="pdfTopTableData"
        style="width: 100%"
        border
        :show-header="false"
        :row-style="topRowStyle"
      >
        <el-table-column
          v-for="(item, key) in pdfTopTableFields"
          :key="key"
          :prop="item"
          :label="key"
        >
        </el-table-column>
      </el-table>
      <!-- 顶部表格数据 start -->

      <!-- 账户表格数据 start -->
      <el-table
        :data="pdfAccountTableData"
        style="width: 100%; border-top: none;"
        border
        :show-header="false"
        :span-method="accountSpanMethod"
        v-loading="accountLoading"
      >
        <el-table-column
          v-for="(item, key) in pdfAccountTableFields"
          :key="key"
          :prop="item"
          :label="key"
        >
        </el-table-column>
      </el-table>
      <!-- 账户表格数据 start -->

      <!-- 展示表格数据 start -->
      <el-table
        :data="pdfTableData"
        style="width: 100%; border-top: none;"
        border
        v-loading="loading"
      >
        <el-table-column
          v-for="(item, key) in pdfTableFields"
          :key="key"
          :prop="item"
          :label="key"
          align="center"
        >
        </el-table-column>
      </el-table>
      <!-- 展示表格数据 start -->

      <!-- 前两列合并 start -->
      <!-- <el-table :data="pdfMergeTableData" style="width: 100%; border-top: none;" :span-method="arraySpanMethod" border :show-header="false" :row-style="rowStyle">
        <el-table-column v-for="(item, key) in pdfTableFields" :key="key" :prop="item" :label="key" align="center">
        </el-table-column>
      </el-table> -->
      <!-- 前两列合并 end -->

      <!-- 整行合并 start -->
      <el-table
        :data="pdfRowMergeTableData"
        style="width: 100%; border-top: none;"
        border
        :show-header="false"
        :row-style="rowStyle"
      >
        <el-table-column
          v-for="(item, key) in pdfRowTableFields"
          :key="key"
          :prop="item"
          :label="key"
          align="center"
        >
        </el-table-column>
      </el-table>
      <!-- 整行合并 end -->

      <!-- 左右两侧 start -->
      <el-table
        :data="pdfTwoFlankTableData"
        style="width: 100%; border-top: none;"
        border
        :show-header="false"
      >
        <el-table-column
          v-for="(item, key) in pdfTwoFlankTableFields"
          :key="key"
          :prop="item"
          :label="key"
        >
          <template slot-scope="scope">
            <div>
              <template v-if="item == 'dataMatch'">
                <div class="data-text">{{ scope.row.dataMatch }}</div>
                <div class="content-text"></div>
                <div class="company-sign">
                  <div>（公司签章）</div>
                  <div class="manager">经办人：</div>
                  <div>年　　月　　日</div>
                </div>
              </template>
              <template v-else-if="item == 'dataNoMatch'">
                <div class="data-text">{{ scope.row.dataNoMatch }}</div>
                <div class="content-text"></div>
                <div class="company-sign">
                  <div>（公司签章）</div>
                  <div class="manager">经办人：</div>
                  <div>年　　月　　日</div>
                </div>
              </template>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 左右两侧 end -->

      <!-- 整行 start -->
      <el-table
        :data="pdfExplainTableData"
        style="width: 100%; border-top: none;"
        border
        :show-header="false"
      >
        <el-table-column
          v-for="(item, key) in pdfExplainTableFields"
          :key="key"
          :prop="item"
          :label="key"
        >
          <template slot-scope="scope">
            <div class="explain">
              <div style="width: 100%;">{{ scope.row.explain }}</div>
              <div class="explain-footer">
                <div>{{ footerData.companyName }}</div>
                <div>{{ footerData.date }}</div>
              </div>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 整行 end -->
    </div>
  </div>
</template>

<script>
import { formatDateTime, formatDate, formatMonth } from '../utils/utils.js'

export default {
  name: 'PdfStreamAccount',
  props: [
    'companyId',
    'companyName',
    'spotId',
    'startEndDate',
    'type',
    'warrantyState',
    'errorRecordsUpkeepCost'
  ],
  data() {
    return {
      loading: false,
      downloadLoading: false, // 导出pdf按钮的加载动画
      verifyLoading: false, // 核实按钮的加载动画
      pdfUrl: '',
      pdfName: '',

      pdfTopTableFields: {
        表格内容: 'content'
      },
      pdfTopTableData: [
        {
          content: '平遥古城景区管理有限公司：'
        },
        {
          content:
            '为了保证会计数据的准确性、及时性，依据后台管理系统订单数据，现将对账期间内与贵公司尚未结算的月度数据列示，如与贵公司核算数据相符，请在本函下方“数据相符”处盖章，如有不符请在“数据不符”处列明不符内容并签章。'
        }
      ], // 顶部表格数据

      pdfAccountTableFields: {
        字段名称: 'fieldName',
        数据名称: 'dataName'
      },
      pdfAccountTableData: [
        {
          fieldName: '收款账户',
          dataName: '平遥古城景区管理有限公司'
        },
        {
          fieldName: '收款账号',
          dataName: '0508 0144 0920 0102 334'
        },
        {
          fieldName: '开 户 行',
          dataName: '中国工商银行股份有限公司平遥支行'
        }
      ], // 账户表格数据
      accountLoading: false, // 账户列表加载状态

      pdfTableFields: {
        //导出Excel表格的表头设置
        系统中景区名称: '景区名称',
        月份: '月份',
        景区承担配件费: '配件费用',
        景区承担维修费: '维修费用',
        费用合计: '合计'
      },
      pdfTableData: [], // pdf流水对账单表格数据

      pdfMergeTableData: [
        {
          scenicName: '分润金额小计（含税）：',
          month: '',
          accessoryApplicationFee: '103356.00',
          tencnetFee: '620.14',
          canFenRunAmount: '102735.86',
          fenRunAmount: '20547.17'
        },
        {
          scenicName: '分润比例：',
          month: '',
          accessoryApplicationFee: '',
          tencnetFee: '',
          canFenRunAmount: '',
          fenRunAmount: '20%'
        },
        {
          scenicName: '流水结算金额（不含税）：',
          month: '',
          accessoryApplicationFee: `可分润金额=流水×（1-0.6%）　
        增值税税额=可分润金额÷（1+6 %）×6%
        `,
          tencnetFee: '',
          canFenRunAmount: '',
          fenRunAmount: '19384.12'
        }
      ], // pdf合并表格数据

      pdfRowTableFields: {
        公式: 'equation'
      },
      pdfRowMergeTableData: [
        {
          equation: '费用合计=景区承担配件费+景区承担维修费'
        }
      ], // pdf整行表格数据

      pdfTwoFlankTableFields: {
        数据相符: 'dataMatch',
        数据不符: 'dataNoMatch'
      },
      pdfTwoFlankTableData: [
        {
          dataMatch: '1、数据相符，核对无误。',
          dataNoMatch: '2、数据不符，以下为不符内容：'
        }
      ], // pdf两列表格数据（数据核对部分）

      pdfExplainTableFields: {
        说明: 'explain'
      },
      pdfExplainTableData: [
        {
          explain:
            '请贵公司尽快核对，并将已签章对账单反馈给我公司负责对账人员。'
        }
      ], // pdf底部说明部分表格数据

      footerData: {
        companyName: '平遥九星科技有限公司',
        date: '2021年9月3日'
      },

      verifyState: '0'
    }
  },
  created() {
    this.pdfTopTableData[0].content = this.companyName + '：'
    this.pdfAccountTableData[0].dataName = this.companyName
    this.footerData.companyName = this.companyName
    this.footerData.date = formatDate()

    this.getAccount()
    this.getPdfTableData()
    this.getVerifyState()
  },
  methods: {
    // 获取核实状态
    async getVerifyState() {
      const { data: res } = await this.$http.get(
        '/system/statementOfAccessories/settlementStateState',
        {
          params: {
            company: this.companyId,
            spot: this.spotId,
            beginDate: this.startEndDate && this.startEndDate[0],
            endDate: this.startEndDate && this.startEndDate[1],
            paymentType: this.type || null,
            warrantyState: this.warrantyState || null,
            errorRecordsUpkeepCost: this.errorRecordsUpkeepCost || null
          }
        }
      )
      // console.log(res)
      if (res.state != 200) {
        this.$message({
          type: 'error',
          message: '请求失败！'
        })
        return
      }

      // 状态模拟
      var verifyState = res.data // 0为已核实，1为未核实
      if (verifyState == '0') {
        console.log('已核实')
        this.verifyState = '0'
      } else if (verifyState == '1') {
        console.log('未核实')
        this.verifyState = '1'
      }
    },

    /**
     * 修改核实状态
     */
    async exitSettlementState() {
      this.verifyLoading = true

      const { data: res } = await this.$http.get(
        '/system/statementOfAccessories/exitSettlementState',
        {
          params: {
            company: this.companyId,
            spot: this.spotId,
            beginDate: this.startEndDate,
            paymentType: this.type || null,
            warrantyState: this.warrantyState || null,
            errorRecordsUpkeepCost: this.errorRecordsUpkeepCost || null
          }
        }
      )
      // console.log(res)
      if (res.state != 200) {
        this.$message({
          type: 'error',
          message: '请求失败！'
        })
        return
      }

      this.$message({
        type: 'success',
        message: '核实成功！'
      })

      this.verifyLoading = false
    },

    // 下载pdf
    onDownloadPDF() {
      this.$confirm('确认导出PDF文件?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.downloadLoading = true
          var promiseObj = this.$PDFOutput(this.$refs['tableContent'])
          promiseObj.then((value) => {
            const a = document.createElement('a')
            a.href = value
            a.download = '配件维修对账单'
            a.click()

            this.downloadLoading = false
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消导出PDF文件'
          })
        })
    },
    // 核实
    onVerify() {
      // console.log('点击了核实按钮')
      // 如果选择的时间大于等于当月，则不能核实
      if (+new Date(this.startEndDate) >= +new Date(formatMonth())) {
        this.$message({
          type: 'warning',
          message: '不能核实当月及大于当月的数据',
          offset: 100,
          center: true,
          duration: 2000
        })
        return
      }

      if(new Date().getDate() <= 5) {
        this.$message({
          type: 'warning',
          message: '请在5号后再进行对账操作',
          offset: 100,
          center: true,
          duration: 2000
        })
        return
      }
      this.exitSettlementState()
    },

    // 根据公司id获取账户
    async getAccount() {
      this.accountLoading = true

      const { data: res } = await this.$http.get(
        '/system/statementOfAccessories/getCompanyId',
        {
          params: {
            companyId: this.companyId
          }
        }
      )
      // console.log(res)
      if (res.state != 200) {
        this.$message({
          type: 'error',
          message: '请求失败！'
        })
        return
      }

      var data = res.data || {}
      console.log('账户数据：', data)

      this.pdfAccountTableData[1].dataName = data.collectionAccountNumber

      this.pdfAccountTableData[2].dataName = data.bank

      this.accountLoading = false
    },

    // 获取列表数据
    async getPdfTableData() {
      this.loading = true

      const { data: res } = await this.$http.get(
        '/system/statementOfAccessories/preview',
        {
          params: {
            // ...this.searchValidateForm,
            company: this.companyId,
            spot: this.spotId,
            beginDate: this.startEndDate && this.startEndDate[0],
            endDate: this.startEndDate && this.startEndDate[1],
            paymentType: this.type,
            warrantyState: this.warrantyState,
            errorRecordsUpkeepCost: this.errorRecordsUpkeepCost
          }
        }
      )
      // console.log(res)
      if (res.state != 200) {
        this.$message({
          type: 'error',
          message: '请求失败！'
        })
        return
      }

      this.pdfTableData = res.data

      this.loading = false
    },

    topRowStyle({ row, rowIndex }) {
      if (rowIndex == 0) {
        return {
          'font-weight': '800'
        }
      }
    },
    accountSpanMethod({ row, column, rowIndex, columnIndex }) {
      // 合并第一列
      if (columnIndex === 1) {
        return [1, 5]
      } else if (
        columnIndex === 2 ||
        columnIndex === 3 ||
        columnIndex === 4 ||
        columnIndex === 5
      ) {
      }
    },
    // pdf流水对账单表格合并
    arraySpanMethod({ row, column, rowIndex, columnIndex }) {
      // 合并第0列
      if (columnIndex === 0) {
        return [1, 2]
      } else if (columnIndex === 1) {
        // 将第一列设为0
        return [0, 0]
      }

      // 合并2行2列
      if (rowIndex === 2 && columnIndex === 2) {
        return [1, 3]
      } else if (rowIndex === 2) {
        if (columnIndex === 3 || columnIndex === 4) {
          return [0, 0]
        }
      }
    },

    // pdfMergeTableData表格的样式设置
    rowStyle({ row, rowIndex }) {
      return {
        'font-weight': '800'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.pdf-operate-btn {
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.data-text {
  line-height: 30px;
  font-weight: 800;
}
.content-text {
  line-height: 30px;
  margin-top: 30px;
  margin-bottom: 30px;
  height: 100px;
}
.company-sign {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  margin-right: 100px;
  line-height: 30px;
}

.company-sign .manager {
  width: 150px;
}

.explain {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
}

.explain-footer {
  line-height: 30px;
  margin-top: 30px;
  margin-right: 100px;
}
</style>
