<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
        >
          <!-- 请选择公司 start -->
          <el-form-item label="" prop="company">
            <el-select
              v-model="searchValidateForm.company"
              filterable
              placeholder="请选择公司"
              clearable
              @change="onChangeSearchCompany"
            >
              <el-option
                v-for="(item, index) in searchCompanyOptions"
                :key="index"
                :label="item.COMPANY_NAME"
                :value="item.COMPANY_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择公司 end -->

          <!-- 请选择景区 start -->
          <el-form-item label="" prop="spot">
            <el-select
              v-model="searchValidateForm.spot"
              filterable
              placeholder="请选择景区"
              clearable
              @change="onChangeSearchSpot"
            >
              <el-option
                v-for="(item, index) in searchScenicOptions"
                :key="index"
                :label="item.SPOT_NAME"
                :value="item.SPOT_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择景区 end -->

          <!-- 请选择日期统计方式 start -->
          <!-- <el-form-item
            label=""
            prop=""
            :rules="[{ max: 50, message: '景区名称长度不能大于50个字符' }]"
          >
            <el-select
              v-model="selectDateState"
              filterable
              placeholder="请选择日期统计方式"
              @change="onDateChange"
            >
              <el-option
                v-for="(item, index) in dateStateOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item> -->
          <!-- 请选择日期统计方式 end -->

          <!-- 输入起止时间 start -->
          <el-form-item label="" prop="" :rules="[]">
            <!-- <el-date-picker
              v-model="searchStartEndDate"
              type="month"
              placeholder="选择月"
              value-format="yyyy-MM"
            >
            </el-date-picker> -->

            <el-date-picker
              v-model="searchStartEndDate"
              unlink-panels
              :type="dateType"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              value-format="yyyy-MM"
            >
            </el-date-picker>
          </el-form-item>
          <!-- 输入起止时间 end -->

          <!-- 请选择类型 start -->
          <el-form-item label="" prop="type" :rules="[]">
            <el-select
              v-model="searchValidateForm.type"
              collapse-tags
              style="min-width: 165px"
              placeholder="请选择类型"
              clearable
            >
              <el-option
                v-for="item in typeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择类型 end -->

          <!-- 请选择质保状态 start -->
          <el-form-item label="" prop="warrantyState" :rules="[]">
            <el-select
              v-model="searchValidateForm.warrantyState"
              collapse-tags
              style="min-width: 165px"
              placeholder="请选择质保状态"
              clearable
            >
              <el-option
                v-for="item in warrantyStateOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择质保状态 end -->

          <!-- 输入配件维修费 start -->
          <el-form-item
            label=""
            prop="errorRecordsUpkeepCost"
            :rules="[
              {
                type: 'number',
                message: '实际总收入金额必须为数字值',
                transform: (value) => Number(value)
              }
            ]"
          >
            <el-input
              type="number"
              v-model.number="searchValidateForm.errorRecordsUpkeepCost"
              placeholder="请输入配件维修费"
            ></el-input>
          </el-form-item>
          <!-- 输入配件维修费 end -->
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 搜索 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 搜索 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 预览 start -->
        <el-button
          type="primary"
          icon="el-icon-view
"
          @click="onPreview"
          >预览</el-button
        >
        <!-- 预览 end -->

        <!-- 导出 Excel start -->
        <el-button
          type="primary"
          :loading="downloadLoading"
          icon="el-icon-top-right"
          @click="exportExcel"
          >导出Excel</el-button
        >
        <!-- 导出 Excel end -->
      </el-header>
      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
          v-loading="tableLoading"
          show-summary
          :summary-method="getSummaries"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
          show-summary
          :summary-method="getSummaries"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>
      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40, 50, 60, 70, 80, 90]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <el-dialog
        title="配件维修对账单"
        :visible.sync="pdfDialogVisible"
        :before-close="handleClose"
        center
        fullscreen
        class="pdf-dialog"
        v-if="pdfDialogVisible"
      >
        <!-- 配件对账单表格 start -->
        <pdf-stream-account
          :companyId="searchValidateForm.company"
          :companyName="searchCompanyName"
          :spotId="searchValidateForm.spot"
          :startEndDate="searchStartEndDate"
          :type="searchValidateForm.type"
          :warrantyState="searchValidateForm.warrantyState"
          :errorRecordsUpkeepCost="searchValidateForm.errorRecordsUpkeepCost"
        ></pdf-stream-account>
        <!-- 配件对账单表格 end -->

        <!-- 底部取消确定按钮 start -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="pdfDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="pdfDialogVisible = false"
            >确 定</el-button
          >
        </span>
        <!-- 底部取消确定按钮 start -->
      </el-dialog>
    </el-container>
  </div>
</template>

<script>
import PdfStreamAccount from '@/components/PdfStreamAccount.vue'
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { formatDateTime } from '../utils/utils.js'
export default {
  name: 'AccessoryAccount',
  data() {
    return {
      tableLoading: false,

      tableFields: {
        //导出Excel表格的表头设置
        类型: 'type',
        景区名称: 'scenicSpotName',
        申请人: 'userName',
        申请时间: 'applyTime',
        确认时间: 'confirmTime', // 暂无
        配件名称: 'accessoryName',
        配件数量: 'errorRecordsNumber',
        是否使用新配件: 'errorRecordsReplace',
        配件是否过保: 'warrantyState', // 暂无
        运营不当造成: 'improperOperation',
        配件是否签收: 'errorRecordsReceive',
        // '已支付配件费/元': 'paidAccessoryFee',
        '配件价格/元': 'errorRecordsPartPrice',
        '维修费用/元': 'errorRecordsUpkeepCost',
        维修状态: 'serviceRecordsResult',
        // "配件费用承担方": "accessoryFeeUndertaker",
        // "维修费用承担方": "maintenanceFeeUndertaker",
        '配件费用/元': 'accessoryFee',
        '(运营+景区)承担配件费/元': 'spotPartsCost',
        '(运营+景区)承担维修费/元': 'spotRepairCost',
        关联单号: '关联单号字段',
        // '运营承担配件费/元': '运营承担配件费字段',
        // '运营承担维修费/元': '运营承担维修费字段',
        结算状态: 'settleState' // 暂无
      },
      tableData: [], // 表格数据

      calculateTotalData: {}, // 合计数据

      searchCompanyOptions: [], // 公司选项
      searchScenicOptions: [], // 景区选项
      dateStateOptions: [
        {
          value: '1',
          label: '按月查询'
        },
        {
          value: '2',
          label: '按日查询'
        },
        {
          value: '3',
          label: '按年查询'
        }
      ], // 日期统计方式选项
      typeOptions: [
        {
          value: '1',
          label: '配件申请'
        },
        {
          value: '2',
          label: '故障上报'
        },
        {
          value: '3',
          label: '损坏赔偿'
        }
      ], // 配件类型选项
      warrantyStateOptions: [
        {
          value: '1',
          label: '在质保期'
        },
        {
          value: '2',
          label: '不在质保期'
        }
      ], // 支付状态选项

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      total: 0, // 总页数
      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      selectDateState: '1', // 日期统计方式（月，日，年）
      dateType: 'monthrange', // 日期类型（月，日，年）
      searchValidateForm: {}, // 搜索表单验证对象
      validateForm: {}, // 表单验证对象

      searchCompanyName: '', // 搜索的公司
      searchScenicName: '', // 搜索的景区
      searchStartEndDate: '', // 搜索的起止时间
      searchAccessoryType: '', // 搜索的配件类型
      searchWarrantyState: '', // 搜索的质保状态
      searchMaintenanceFee: '', // 搜索的配件维修费

      pdfDialogVisible: false

      // uploadDialog:
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    this.getSearchValidateForm()

    this.initTableData()

    this.getCompanyList()
    this.getScenicList()
  },
  mounted() {},
  methods: {
    // 获取搜索表单验证对象
    getSearchValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        ...validateForm,
        company: '',
        spot: ''
      }
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 根据景区ID获取公司列表（搜索时公司列表）
     */
    async getCompanyList(spotId) {
      if (!spotId) {
        spotId = null
      }
      const { data: res } = await this.$http.get(
        '/system/subscriptionInformation/companyList',
        {
          params: {
            spotId: spotId
          }
        }
      )
      if (res.state == '200') {
        // console.log(res);
        this.searchCompanyOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 根据公司ID获取景区列表（搜索时景区列表）
     */
    async getScenicList(companyId) {
      if (!companyId) {
        companyId = null
      }
      // 这里使用的是`签约信息`中的`景区列表`接口
      const { data: res } = await this.$http.get(
        '/system/subscriptionInformation/spotList',
        {
          params: {
            companyId: companyId
          }
        }
      )
      if (res.state == '200') {
        // console.log(res);
        this.searchScenicOptions = res.data
        return res
      } else {
        return
      }
    },

    // 获取合计数据
    async getCalculateTotal() {
      const { data: res } = await this.$http.get(
        '/system/statementOfAccessories/calculateTotal',
        {
          params: {
            ...this.searchValidateForm,
            beginDate: this.searchStartEndDate && this.searchStartEndDate[0],
            endDate: this.searchStartEndDate && this.searchStartEndDate[1],
            paymentType: this.searchValidateForm.type, // 类型
            pageNum: this.currentPage, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
      // console.log(res)
      if (res.state != 200) {
        this.$message({
          type: 'error',
          message: '请求失败！'
        })
        return
      }
      this.calculateTotalData = res.data || {}
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      this.tableLoading = true

      const { data: res } = await this.$http.get(
        '/system/statementOfAccessories/list',
        {
          params: {
            ...this.searchValidateForm,
            beginDate: this.searchStartEndDate && this.searchStartEndDate[0],
            endDate: this.searchStartEndDate && this.searchStartEndDate[1],
            paymentType: this.searchValidateForm.type, // 类型
            pageNum: this.currentPage, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
      // console.log(res)
      if (res.state != 200) {
        this.$message({
          type: 'error',
          message: '请求失败！'
        })
        return
      }
      this.tableData = res.data
      this.total = res.total

      this.getCalculateTotal()

      this.tableLoading = false
    },

    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
      const { data: res } = await this.$http.get(
        '/system/statementOfAccessories/download',
        {
          params: {},
          responseType: 'arraybuffer'
        }
      )
      // console.log(res)
      const url = window.URL.createObjectURL(
        new Blob([res], {
          type:
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
        })
      )
      const a = document.createElement('a')
      console.log(url)
      a.href = url
      a.download = '配件对账单'
      a.click()
      this.downloadLoading = false
    },

    /** 搜索表单中选项变化方法 start */
    // 公司选项变化时触发
    async onChangeSearchCompany(companyId) {
      if (!companyId) {
        this.searchCompanyName = ''
      }
      this.searchCompanyOptions.forEach((item) => {
        if (item.COMPANY_ID == companyId) {
          this.searchCompanyName = item.COMPANY_NAME
        }
      })

      var res = await this.getScenicList(companyId)
      var searchScenicOptions = res.data

      this.searchScenicOptions = searchScenicOptions

      // 如果返回的景区列表为空，则直接返回
      if (searchScenicOptions.length == 0) {
        this.searchValidateForm.spot = ''
        return
      }

      var isHasScenic = false
      searchScenicOptions.forEach((item) => {
        if (item.SPOT_ID == this.searchValidateForm.spot) {
          isHasScenic = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则景区，则为搜索景区选择框设置默认值
      var spotId = searchScenicOptions[0].SPOT_ID /* 获取第一个景区id */
      if (!!companyId && !isHasScenic) {
        this.searchValidateForm.spot = spotId
      }
    },

    // 景区选项变化时触发(搜索)
    async onChangeSearchSpot(spotId) {
      var res = await this.getCompanyList(spotId)
      // console.log(res.data);
      var searchCompanyOptions = res.data

      // 如果返回的公司列表为空，则直接返回
      if (searchCompanyOptions.length == 0) {
        this.searchValidateForm.company = ''
        return
      }

      var isHasCompany = false
      searchCompanyOptions.forEach((item) => {
        if (item.COMPANY_ID == this.searchValidateForm.company) {
          isHasCompany = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则公司，则为搜索公司选择框设置默认值
      if (!!spotId && !isHasCompany) {
        this.searchValidateForm.company = searchCompanyOptions[0].COMPANY_ID

        this.searchCompanyName = searchCompanyOptions[0].COMPANY_NAME
      }
    },
    /** 搜索表单中选项变化方法 end */

    // 日期选项值发生变化时触发
    onDateChange() {
      console.log(this.selectDateState)
      if (this.selectDateState == '1') {
        // 按月查询
        this.dateType = 'monthrange'
      } else if (this.selectDateState == '2') {
        // 按日查询
        this.dateType = 'daterange'
      } else if (this.selectDateState == '3') {
        // 按年查询
        this.dateType = 'yearrange'
      }
    },

    // 点击搜索按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.initTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 重置
    onReset() {
      // 重新获取表单
      this.getSearchValidateForm()

      this.searchStartEndDate = ''
      this.currentPage = 1

      // 重新获取表格数据
      this.initTableData()
      // 重新获取搜索公司列表
      this.getCompanyList()
      // 重新获取搜索景区列表
      this.getScenicList()
    },

    // 预览
    onPreview() {
      // 判断录入的起止时间是否是按月查询，若不是提示“查询起止时间非整月”
      if (
        !this.searchStartEndDate ||
        !this.searchValidateForm.company ||
        !this.searchValidateForm.spot
      ) {
        this.$message({
          type: 'warning',
          message: '一次只能核对一个景区的整月对账单',
          offset: 100,
          center: true,
          duration: 1000
        })
      } else {
        // 打开PDF对账单
        this.pdfDialogVisible = true
      }
    },

    // 导出Excel表格
    exportExcel() {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.downloadLoading = true
          await this.getExportTableData()
          this.$message({
            type: 'success',
            message: '已成功导出Excel表格！',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    /**
     * 自定义合计行
     */
    getSummaries(param) {
      const { columns, data } = param
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '合计'
          // return
        }
        // const values = data.map((item) => Number(item[column.property]))
        // if (!values.every((value) => isNaN(value))) {
        //   sums[index] = values.reduce((prev, curr) => {
        //     const value = Number(curr)
        //     if (!isNaN(value)) {
        //       return prev + curr
        //     } else {
        //       return prev
        //     }
        //   }, 0)
        //   sums[index] = ''
        // } else {
        //   // sums[index] = '';
        // }

        // console.log(column)

        // 维修费用
        if (column.property == 'errorRecordsUpkeepCost') {
          sums[index] = this.calculateTotalData.totalMaintenanceCost + '元'
        }

        // 配件费用
        if (column.property == 'accessoryFee') {
          sums[index] = this.calculateTotalData.totalCostOfAccessories + '元'
        }

        // 景区承担配件费
        if (column.property == 'spotPartsCost') {
          sums[index] = this.calculateTotalData.spotBearPartsCost + '元'
        }

        // 景区承担维修费
        if (column.property == 'spotRepairCost') {
          sums[index] = this.calculateTotalData.spotBearrepairCost + '元'
        }
      })

      return sums
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },

    // 点击右上角关闭pdf弹窗
    handleClose(done) {
      // console.log(done);
      this.pdfDialogVisible = false
    }
  },
  components: {
    PdfStreamAccount
    // DownloadExcel: JsonExcel,
  },
  computed: {
    tableContentValue() {
      return (row, value) => {
        var result = ''
        if (value == 'type') {
          // 类型
          if (row[value] == 1) {
            result = '配件申请'
          } else if (row[value] == 2) {
            result = '故障上报'
          } else if (row[value] == 3) {
            result = '损坏赔偿'
          }
        } else if (value == 'errorRecordsReplace') {
          // 是否使用新配件
          if (row[value] == 0) {
            result = '否'
          } else if (row[value] == 1) {
            result = '是'
          }
        } else if (value == 'errorRecordsReceive') {
          // 配件是否签收
          if (row[value] == 0) {
            result = '否'
          } else if (row[value] == 1) {
            result = '是'
          }
        } else if (value == 'serviceRecordsResult') {
          // 维修状态
          if (row[value] == 1) {
            result = '未维修'
          } else if (row[value] == 2) {
            result = '已修好，远程协助'
          } else if (row[value] == 3) {
            result = '已判断问题，需要配件'
          } else if (row[value] == 4) {
            result = '未修好，需要售后现场解决'
          } else if (row[value] == 5) {
            result = '已修好，售后现场解决'
          } else if (row[value] == 6) {
            result = '已确认修好'
          } else if (row[value] == 7) {
            result = '已关闭'
          }
        } else {
          result = row[value]
        }
        return result
      }
    }
  }
}
</script>

